@import url("https://fonts.googleapis.com/css2?family=Noto+Sans:wght@400;700&display=swap");

.back {
  position: absolute;
  min-height: 100%;
  filter: brightness(63%);
}
.back > img {
  width: 100vw;
  min-height: 100px;
  overflow: hidden;
  transition: transform 0.8s ease;
}

.title {
  position: relative;
  display: flex;
  flex-direction: column;
  padding: 21% 3%;
}

.title > h1 {
  all: unset;
  font-size: 97px;
  font-weight: 700;
  font-family: "Noto Sans", sans-serif;
}

.title > h4 {
  all: unset;
  font-size: 38px;
  font-family: "Noto Sans", sans-serif;
}

.free {
  all: unset;
  background-color: red;
  color: white;
  width: 276px;
  height: 35px;
  margin-top: 31px;
  text-align: center;
  border-radius: 6px;
  font-family: "Noto Sans", sans-serif;
  font-weight: 700;
  cursor: pointer;
  padding-top: 13px;
}

.login__button {
  position: absolute;
  display: flex;
  flex-direction: row;
  padding-left: 90%;
  z-index: 1;
  margin-top: 10px;
}

.login__button > .login {
  all: unset;
  background-color: red;
  color: #e4e2e2;
  width: 74px;
  padding-top: 5px;
  height: 25px;
  margin-top: 25px;
  border-radius: 4px;
  font-family: "Noto Sans", sans-serif;
  font-size: 13px;
  cursor: pointer;
  font-weight: 700;
  text-align: center;
}

.free:hover {
  transition: all 0.3s ease-in-out;
  filter: brightness(77%);
}

.login:hover {
  transition: all 0.3s ease-in-out;
  filter: brightness(85%);
}

@media screen and (max-width: 1000px) {
  .back > img {
    width: 110vw;
    height: 110vh;
  }
  .nav > .a {
    display: none;
  }

  .title > h1 {
    font-size: 65px;
  }
  .title {
    padding: 70% 7%;
  }
  .title > h4 {
    width: 200px;
    font-size: 20px;
  }
  .login__button {
    display: none;
  }
  .navbar > img {
    width: 120px;
}
.free {
  background-color: red;
  color: white;
  width: 240px;
  height: 33px;
  margin-top: 31px;
 
}
 
}
